.fade-up {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease, transform 0.5s ease;
}

.fade-up-active {
  opacity: 1;
  transform: translateY(0);
}

body {
  background-image: url('img/bg.jpg');
  background-size: auto;
  background-repeat: repeat;
  background-position: center;
  margin: 0;
  font-family: "Lato", sans-serif;
  overflow-y: auto;
}

.experience-container {
  padding: 80px 20px;
  text-align: center;
  min-height: 100vh;
  box-sizing: border-box;
}

.experience-header {
  margin-bottom: 40px;
}

.experience-title {
  font-family: "Righteous", sans-serif;
  font-size: 2.5rem;
  color: white;
}

.category-toggle {
  margin-bottom: 40px;
}

.toggle-button {
  background-color: white;
  color: #ff7f00;
  padding: 15px 30px;
  font-size: 1.2rem;
  border: none;
  border-radius: 50px;
  margin: 0 15px;
  cursor: pointer;
  font-family: "Righteous", sans-serif;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.toggle-button.active {
  background: linear-gradient(135deg, #4b0082, #00008b, #000000);
  color: white;
}

.projects-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.project-card {
  background: linear-gradient(135deg, #4b0082, #00008b, #000000);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  animation: fadeIn 0.8s ease-in-out;
}

.project-card img,
.project-card video {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
}

.project-card h3 {
  color: #00ff00;
  font-size: 1.4rem;
  margin: 20px 0;
}

@media (min-width: 768px) {
  .projects-container {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }
}

@media (max-width: 768px) {
  .projects-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .projects-container {
    grid-template-columns: 1fr;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
}

.modal-content {
  background: linear-gradient(135deg, #4b0082, #00008b, #000000);
  border-radius: 10px;
  max-width: 90%;
  width: 900px;
  max-height: 90%;
  padding: 20px;
  text-align: center;
  color: white;
  overflow: auto;
  animation: fadeIn 1s ease-in-out;
}

.modal-content img {
  width: 100%;
  height: auto;
  max-height: 300px;
  object-fit: cover;
  border-radius: 8px;
}

.modal-content h3 {
  margin-top: 20px;
  color: #00ff00;
  font-size: 1.6rem;
}

.modal-content p {
  font-size: 1rem;
  margin-top: 20px;
  line-height: 1.5;
}

.close-btn {
  background-color: #ff4500;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}

.close-btn:hover {
  background-color: #ff6347;
}

.modal-content video {
  width: 100%;
  height: auto;
  max-height: 400px;
  border-radius: 8px;
}

.skills-section {
  padding: 10px 20px;
  background-color: transparent;
  text-align: center;
  margin-top: 0;
}

.skills-section h2 {
  font-family: "Righteous", sans-serif;
  font-size: 2.4rem;
  color: #ffffff;
  margin-top: 0;
  margin-bottom: 15px;
}

.skills-section ul {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
  margin: 0;
}

.skills-section li {
  background-color: #ffffff;
  padding: 12px 20px;
  border-radius: 10px;
  font-size: 1rem;
  color: #333333;
  transition: background-color 0.3s ease, transform 0.3s ease;
  display: flex;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin: 8px;
  animation: fadeIn 0.8s ease-in-out;
}

.skills-section li:hover {
  background-color: #f0f0f0;
  transform: translateY(-3px);
}

.skill-icon {
  width: 30px;
  height: auto;
  margin-right: 8px;
}

@media (max-width: 768px) {
  .skills-section h2 {
    font-size: 2rem;
  }

  .skills-section li {
    font-size: 0.9rem;
    padding: 10px 15px;
  }

  .skill-icon {
    width: 25px;
  }
}

.footer-section {
  padding: 40px 20px;
  background-color: transparent;
  text-align: center;
  margin-top: 40px;
}

.footer-section h2 {
  font-family: "Righteous", sans-serif;
  font-size: 2rem;
  color: #ffffff;
  margin-bottom: 20px;
}

.footer-section p {
  font-size: 1rem;
  color: #ffffff;
  margin-bottom: 30px;
}

.footer-section .social-links {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
  margin: 0;
}

.footer-section .social-links a {
  background-color: #ffffff;
  color: #333333;
  padding: 12px 20px;
  border-radius: 50px;
  font-size: 1rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s ease, transform 0.3s ease;
  animation: fadeIn 0.8s ease-in-out;
}

.footer-section .social-links a:hover {
  background-color: #f0f0f0;
  transform: translateY(-3px);
}

.footer-section .social-links a img {
  width: 20px;
  height: auto;
  margin-right: 8px;
}

@media (max-width: 768px) {
  .footer-section h2 {
    font-size: 1.8rem;
  }

  .footer-section p {
    font-size: 0.9rem;
  }

  .footer-section .social-links a {
    font-size: 0.9rem;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}